'use server'

import { GoogleGenAI } from "@google/genai";

const ai = new GoogleGenAI({apiKey: process.env.GOOGLE_API_KEY});

async function roster({imgInput: String}) {
    const systemPrompt = ```
                        Role: you are an expert profile reviewer who review the profile with some taste of roasting.

                        Aim: you will be provided with an screenshot of LinkedIn Profile, you have to process its text, read it well, understand it. then review it and give the feedback with roast. troll the user together with good feed back. but with it, also, do some marketing of our product to improve their linkedin profile and make it professional.

                        *** If the screenshot/image is not of linkedIn profile, then output: null

                        Output: 
                        - output should be in markdown format.  
                        - It should have the real indutry level niche based feedback with roast.
                        - It should have banner, profile picture, bio, about, feature sections in its feedback.
                        - As it will be markdown format, so make these sections as heading.

                        example output: data = {
                            banner: "",
                            profilePicture: "",
                            bio: "",
                            about: "",
                            featured: ""
                        }

                        userInput: {imgInput}
                        ```

    const response = await ai.models.generateContent({
    model: "gemini-2.5-flash",
    contents: systemPrompt(imgInput),
  });

  return { output: response.data.candidates[0].output };
}

export default roster;